@import './../../../styles/variables';

ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  button{
    color: $text-color;
    text-decoration: none;;
    outline: none;
    &:hover{
      cursor: pointer;
    }
  }
  
  /* options bar */
  
  /* <nav> */
  .navbar {
    height: $options-size;
    // background-color: $bg;
    padding: 0 1rem;
  }
  
  /* <ul> */
  .navbar-nav {
    max-width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
  }
  
  /* <li> */
  .nav-item {
    position: relative;
    width: 48px;
    // width: calc($options-size * 0.8);
    display: flex;
    align-items: center;
    justify-content: center;

  }
  
  /* Icon Button */
  .icon-button {
    // --button-size: calc($options-size * 0.5);
    // width: var(--button-size);
    // height: var(--button-size);
    // background-color: #484a4d;
    border-radius: 50%;
    padding: 5px;
    margin: 2px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: filter 300ms;
  }
  
  .icon-button:hover {
    filter: brightness(1.2);
  }
  
  .icon-button i { 
    fill: $text-color;
    width: 20px;
    height: 20px;
  }
  
  
  /* Dropdown Menu */
  .dropdown {
    position: absolute;
    top: 58px;
    width: 300px;
    transform: translateX(-45%);
    background-color: $bg;
    border: $border;
    border-radius: $border-radiusx2;
    padding: 1rem;
    overflow: hidden;
    transition: height $speed ease;
  }
  
  .menu {
    width: 100%;
  }
  
  .menu-item {
    height: 50px;
    display: flex;
    align-items: center;
    border-radius: $border-radiusx2;
    transition: background $speed;
    padding: 0.5rem;
    cursor: pointer;

  }
  
  .menu-item .icon-button {
    margin-right: 0.5rem;
  }
  
  .menu-item .icon-button:hover {
    filter: none;
  }
  
  .menu-item:hover {
    background-color: #525357;
  }
  
  .icon-right {
    margin-left: auto;
  }
  
  /* CSSTransition classes  */
  .menu-primary-enter {
    position: absolute;
    transform: translateX(-110%);
  }
  .menu-primary-enter-active {
    transform: translateX(0%);
    transition: all $speed ease;
  }
  .menu-primary-exit {
    position: absolute;
  }
  .menu-primary-exit-active {
    transform: translateX(-110%);
    transition: all $speed ease;
  }
  
  
  .menu-secondary-enter {
    transform: translateX(110%);
  }
  .menu-secondary-enter-active {
    transform: translateX(0%);
    transition: all $speed ease;
  }
  .menu-secondary-exit {
  
  }
  .menu-secondary-exit-active {
    transform: translateX(110%);
    transition: all $speed ease;
  }

  //cart count
  .cart-count{
    position:absolute;
    top:2px;
    right:-5px;
    width:25px;
    height:25px;
    border-radius: 50%;
    background-color:$red;
    color:$white;
    display: flex;
    justify-content: center;
    align-content: center;
}